import { Component } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { Router, ActivatedRoute, NavigationExtras } from '@angular/router';
import {
  CreatePINInputDto,
  ERROR_MESSAGE,
  RegisterService, ValidationService
} from '@youtap-web-lib';
import { ModalService } from '../../../shared/service/modal/modal.service';
import { RegisterModalComponent } from '../register-modal/register-modal.component';
import { TncModalComponent } from './tnc-modal/tnc-modal.component';

@Component({
  selector: 'pin-register',
  templateUrl: './pin-register.component.html'
})
export class PinRegisterComponent {
  pinForm!: FormGroup;
  loadingOtp = false;
  error = false;
  errorPIN = false;
  pinVisible = false;
  confirmPinVisible = false;
  check = false;

  phone = '';

  constructor(
    private formBuilder: FormBuilder,
    private registerService: RegisterService,
    private router: Router,
    private route: ActivatedRoute,
    private modalService: ModalService
  ) {
    this.pinForm = this.formBuilder.group({
      pin: [
        '',
        Validators.compose([
          Validators.required,
          Validators.maxLength(6),
          Validators.minLength(6),
          ValidationService.numValidator
        ])
      ],
      confirmPin: [
        '',
        Validators.compose([
          Validators.required,
          Validators.maxLength(6),
          Validators.minLength(6),
          ValidationService.numValidator
        ])
      ]
    });
  }

  togglePinVisibility() {
    this.pinVisible = !this.pinVisible;
  }
  toggleConfirmPinVisibility() {
    this.confirmPinVisible = !this.confirmPinVisible;
  }

  submitData() {
    this.loadingOtp = true;
    const modalRef = this.modalService.open(RegisterModalComponent, {
      title: '',
      closeButton: false
    });

    const data: CreatePINInputDto = {
      createPinInput: {
        pin: this.pinForm.controls['pin'].value,
        confirmPin: this.pinForm.controls['confirmPin'].value
      }
    };

    const stateData = {
      title: 'Selamat Kamu Sudah terdaftar!',
      isFromRegister: true
    };
    const navigationExtras: NavigationExtras = {
      state: stateData
    };

    this.registerService.createPINMutation(data).subscribe({
      next: ({ data }: any) => {
        this.loadingOtp = false;
        if (data) {
          modalRef.close('');
          this.router.navigate(
            ['/register/success-register'],
            navigationExtras
          );
        }
      },
      error: (error: any) => {
        modalRef.close('');
        this.loadingOtp = false;
        this.error = true;
        if (error?.message?.toLowerCase() === ERROR_MESSAGE.ERROR_PIN) {
          this.errorPIN = true;
        }
      }
    });
  }

  onChange: any = () => {
    this.errorPIN = false;
  };

  tncModal() {
    const modalRef = this.modalService.open(TncModalComponent, {
      title: 'Syarat dan Ketentuan Umum Layanan',
      closeButton: true
    });
    modalRef.onResult().subscribe((closed) => {
      this.check = closed;
    });
  }

  handleCheck() {
    this.check = !this.check;
  }
}
