import { Component } from '@angular/core';
import { Modal } from '../../../shared/service/modal/modal.model';
import { InputModalDto } from '../../../shared/service/modal/modal.service';

@Component({
  selector: 'header-modal',
  templateUrl: './otp-modal.component.html'
})
export class OtpModalComponent extends Modal {
  title!: string;
  image!: string;
  showButton = false;

  onInjectInputs(inputs: InputModalDto): void {
    this.showButton = inputs.closeButton;
    if (inputs.data) {
      this.title = inputs.data.title;
      this.image = inputs.data.image;
    }
  }

  save(value: string): void {
    this.close(value);
  }

  cancel(): void {
    this.dismiss({});
  }
}
