import { Component } from '@angular/core';
import { Modal } from '../../../../shared/service/modal/modal.model';
import { InputModalDto } from '../../../../shared/service/modal/modal.service';

@Component({
  selector: 'tnc-modal',
  templateUrl: './tnc-modal.component.html'
})
export class TncModalComponent extends Modal {
  title!: string;
  showButton = false;

  onInjectInputs(inputs: InputModalDto): void {
    this.showButton = inputs.closeButton;
  }

  save(value: boolean): void {
    this.close(value);
  }

  cancel(): void {
    this.dismiss({});
  }
}
