import {
  Component,
  EventEmitter,
  forwardRef,
  Input,
  OnInit,
  Output
} from '@angular/core';
import { BusinessCategory, BusinessCategoryService } from '@youtap-web-lib';
import { NG_VALUE_ACCESSOR } from '@angular/forms';

@Component({
  selector: 'business-dropdown',
  templateUrl: './business-category-dropdown.component.html',
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => BusinessCategoryDropdownComponent),
      multi: true
    }
  ]
})
export class BusinessCategoryDropdownComponent implements OnInit {
  value: BusinessCategory = { id: '', displayName: '' };
  id = '';
  dataBusinessCategories: BusinessCategory[] = [];
  @Input() placeHolder = 'Pilih jenis bisnis';
  @Output() valueChange: EventEmitter<any> = new EventEmitter<any>();
  shareModal = false;
  isLoading = true;

  constructor(private businessCategoryService: BusinessCategoryService) {}

  ngOnInit(): void {
    this.getListBusinessCategory();
  }

  openShareModal() {
    if (!this.shareModal) {
      this.shareModal = true;
    }
  }

  changeDrop(data: BusinessCategory) {
    this.valueChange.emit(data);
    this.value = data;
    this.shareModal = false;
  }

  getListBusinessCategory() {
    this.isLoading = true;
    this.businessCategoryService.listBusinessCategory().subscribe({
      next: ({ data }: any) => {
        this.dataBusinessCategories =
          data.listBusinessCategory.businessCategory;

        data.listBusinessCategory.businessCategory.map((val: any) => {
          if (val.id === this.id) {
            this.value = val;
          }
        });
        this.isLoading = false;
      },
      error: (err: any) => {
        this.isLoading = false;
        err;
      }
    });
  }
}
