import { Component } from '@angular/core';
import { Modal } from '../../../../shared/service/modal/modal.model';
import { InputModalDto } from '../../../../shared/service/modal/modal.service';

@Component({
  selector: 'confirmation-modal',
  templateUrl: './confirmation-modal.component.html'
})
export class ConfirmationModalComponent extends Modal {
  title!: string;
  showButton = false;
  data = [
    {
      title: 'Foto e-KTP',
      subtitle: 'Foto e-KTP harus jelas. Kami pastikan data dirimu aman!'
    },
    {
      title: 'Swafoto (selfie) dengan e-KTP',
      subtitle:
        'Pastikan selfiemu jelas, pencahayaan cukup, dan KTP-mu tidak buram.'
    },
    {
      title: 'Tanda Tangan Kamu',
      subtitle: 'Untuk memastikan keaslian kamu sebagai pemilik usaha'
    },
    {
      title: 'Aktifkan pengaturan lokasimu',
      subtitle: 'Untuk mempermudah proses verifikasi akun Youtap kamu'
    }
  ];

  onInjectInputs(inputs: InputModalDto): void {
    this.showButton = inputs.closeButton;
  }

  save(value: string): void {
    this.close(value);
  }

  cancel(): void {
    this.dismiss({});
  }
}
