import { Component } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { AuthData } from '../../../shared/data/auth.data';
import { ModalService } from '../../../shared/service/modal/modal.service';
import { ConfirmationModalComponent } from './confirmation-modal/confirmation-modal.component';
import { AppData } from "../../../shared/data/app-data";
import {LoginKYCInputDto, RegisterService, ValidationService} from "@youtap-web-lib";
import {GaService} from "../../../shared/service/ga.service";

@Component({
  selector: 'login-kyc-register',
  templateUrl: './login-kyc-register.component.html'
})
export class LoginKycRegisterComponent {
  pinVisible = false;
  errorPIN = false;
  error = false;
  loadingOtp = false;
  loginForm!: FormGroup;
  errorMessage = '';

  constructor(
    private formBuilder: FormBuilder,
    private registerService: RegisterService,
    private router: Router,
    private modalService: ModalService,
    private authData: AuthData,
    public appData: AppData,
    private gaService: GaService,
  ) {
    this.loginForm = this.formBuilder.group({
      phone: [
        '',
        Validators.compose([
          Validators.required,
          Validators.minLength(10),
          ValidationService.noSymbolValidator,
          ValidationService.phoneValidator
        ])
      ],
      pin: [
        '',
        Validators.compose([
          Validators.required,
          Validators.minLength(6),
          ValidationService.numValidator
        ])
      ]
    });
  }

  togglePinVisibility() {
    this.pinVisible = !this.pinVisible;
  }
  onChange: any = () => {
    this.errorPIN = false;
    this.errorMessage = ''
  };

  convertToZero(value: string) {
    if (value[0] === '0') {
      return '62' + value.slice(1);
    } else {
      return value;
    }
  }
  submit() {
    this.loadingOtp = true;

    const phone = this.loginForm.controls['phone'].value;
    const data: LoginKYCInputDto = {
      loginInput: {
        phone: this.convertToZero(phone),
        pin: this.loginForm.controls['pin'].value
      }
    };

    this.registerService.loginKYCMutation(data).subscribe({
      next: ({ data }: any) => {
        this.loadingOtp = false;
        this.authData.token = data?.loginKyc.token;
        this.gaService.addEvent('login_after_register_success', 'login_kyc', '')
        if (data) {
          this.router.navigate(['verification'], {
            queryParams: {
              name: data?.loginKyc.merchantRegistration.ownerName
            }
          });
        }
      },
      error: (error) => {
        this.gaService.addEvent('login_after_register_failed', 'login_kyc', '')
        this.errorMessage = error.message;
        this.loadingOtp = false;
        this.error = true;
      }
    });
  }

  save() {
    const modalConfirmationRef = this.modalService.open(
      ConfirmationModalComponent,
      {
        title: '',
        closeButton: false
      }
    );
    modalConfirmationRef.onResult().subscribe((navigate) => {
      if (navigate === 'verification') {
        this.router.navigate(['/'], {});
      }
    });
  }
}
