import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { NavigationExtras, Router } from '@angular/router';
import {
  RegisterService,
  CreateCheckPhoneInputDto,
  ERROR_MESSAGE,
  ValidationService,
  PlatformTypeEnum
} from '@youtap-web-lib';
import { AppData } from '../../shared/data/app-data';
import { AuthData } from '../../shared/data/auth.data';
import { ModalService } from '../../shared/service/modal/modal.service';
import { RegisterModalComponent } from './register-modal/register-modal.component';
import { MetaService } from '../../shared/service/meta.service';
import { EventData } from '../../shared/data/event-data';
import { GaService } from '../../shared/service/ga.service';

@Component({
  selector: 'registration',
  templateUrl: './register.component.html'
})
export class RegisterComponent implements OnInit {
  registerForm!: FormGroup;
  loadingOtp = false;
  errorMessage = '';

  constructor(
    private formBuilder: FormBuilder,
    private registerService: RegisterService,
    private router: Router,
    private modalService: ModalService,
    private authData: AuthData,
    public appData: AppData,
    private metaService: MetaService,
    private eventData: EventData,
    private gaService: GaService
  ) {
    this.registerForm = this.formBuilder.group({
      phone: [
        '',
        Validators.compose([
          Validators.required,
          Validators.minLength(10),
          ValidationService.noSymbolValidator,
          ValidationService.phoneValidator
        ])
      ]
    });
  }

  ngOnInit(): void {
    this.metaService.setMeta({
      title: 'Mulai Usaha dengan Youtap BOS. Register Sekarang!',
      description:
        'Mulai usaha dengan Youtap BOS. Ayo register dan daftarkan usahamu sekarang dan jadi bagian dari ekosistem digital Youtap Indonesia. GRATIS!',
      image:
        'https://storage.googleapis.com/production-youtap-public/banner/IlbfMVveQ=s0.png'
    });
  }

  convertToZero(value: string) {
    if (value[0] === '0') {
      return '62' + value.slice(1);
    } else {
      return value;
    }
  }
  submitPhoneNumber() {
    this.loadingOtp = true;
    const modalRef = this.modalService.open(RegisterModalComponent, {
      title: '',
      closeButton: false
    });
    const phone = this.registerForm.controls['phone'].value;
    this.loadingOtp = true;
    const data: CreateCheckPhoneInputDto = {
      checkPhoneInput: {
        phone: this.convertToZero(phone),
        platformType: PlatformTypeEnum.BOS_REGISTRATION
      }
    };

    const stateData = {
      title: 'Selamat Kamu Sudah terdaftar!',
      isFromRegister: true
    };
    const navigationExtras: NavigationExtras = {
      state: stateData
    };

    this.registerService.createCheckPhoneMutation(data).subscribe({
      // eslint-disable-next-line @typescript-eslint/no-explicit-any
      next: ({ data }: any) => {
        this.loadingOtp = false;
        this.gaService.addEvent(
          'Register_input_number_success',
          'register',
          ''
        );
        if (data) {
          modalRef.close('');
          this.authData.token = data?.checkPhone.token;
          this.eventData.registerExpired = data?.checkPhone.merchantRegistration.otpExpiredAt
          this.router.navigate(['/register/otp'], {
            queryParams: {
              phone: this.convertToZero(phone),
            }
          });
        }
      },
      // eslint-disable-next-line @typescript-eslint/no-explicit-any
      error: (error: any) => {
        modalRef.close('');
        const [graphqlError] = error.graphQLErrors;
        this.errorMessage = graphqlError?.message ? graphqlError?.message : '';
        if (
          graphqlError?.message.toLowerCase() ===
          ERROR_MESSAGE.ALREADY_REGISTERED
        ) {
          this.router.navigate(
            ['/register/success-register'],
            navigationExtras
          );
        }
      }
    });
  }
}
