import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { Location } from '@angular/common';
import { AppData } from '../../../shared/data/app-data';
import { AuthData } from '../../../shared/data/auth.data';
import { GaService } from "../../../shared/service/ga.service";

@Component({
  selector: 'success-basic-register',
  templateUrl: './success-basic-register.component.html',
  styleUrls: ['./success-basic-register.component.scss']
})
export class SuccessBasicRegisterComponent implements OnInit {
  constructor(
    private location: Location,
    private router: Router,
    private route: ActivatedRoute,
    public appData: AppData,
    private authData: AuthData,
    private gaService: GaService,
  ) { }
  title = '';
  isFromPinRegister = false;

  ngOnInit(): void {
    this.route.paramMap.subscribe(() => {
      const state = window.history.state;

      if (state) {
        this.title = state.title;
        this.isFromPinRegister = state.isFromRegister;
      }
    });
  }

  goBack() {
    if (window.history.length < 3 || !this.isFromPinRegister) {
      this.router.navigate(['/']);
    } else {
      this.router.navigate(['/login'], { queryParams: { stateUrl: "/home" } });
    }
  }

  goToLogin() {
    this.gaService.addEvent('go to kyc', 'register', "")
    this.router.navigate(['/']);
    localStorage.removeItem('access_token');
  }

  goTo() {
    this.gaService.addEvent('skip kyc', 'register', "")
    if (this.isFromPinRegister) {
      localStorage.removeItem('access_token')
      this.router.navigate(['/login'], { queryParams: { stateUrl: "/home" } });
    } else {
      this.router.navigate(['/'], {});
    }
  }
}
