import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { LoginKycRegisterComponent } from './login-kyc-register/login-kyc-register.component';
import { MerchantRegisterComponent } from './merchant-register/merchant-register.component';
import { OtpRegisterComponent } from './otp-register/otp-register.component';
import { PinRegisterComponent } from './pin-register/pin-register.component';
import { RegisterComponent } from './register.component';
import { SuccessBasicRegisterComponent } from './success-basic-register/success-basic-register.component';
import { NoLoginGuard } from '../../guard/no-login.guard';
const routes: Routes = [
  {
    path: '',
    canActivate: [NoLoginGuard],
    component: RegisterComponent
  },
  {
    path: 'merchant',
    component: MerchantRegisterComponent
  },
  {
    path: 'otp',
    component: OtpRegisterComponent
  },
  {
    path: 'pin',
    component: PinRegisterComponent
  },
  {
    path: 'login-kyc',
    component: LoginKycRegisterComponent
  },
  {
    path: 'success-register',
    canActivate: [NoLoginGuard],
    component: SuccessBasicRegisterComponent
  }
];
@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class RegisterRoutingModule {}
