import {Component, OnDestroy, OnInit} from '@angular/core';
import {FormGroup, FormBuilder, Validators} from '@angular/forms';
import {Router, ActivatedRoute} from '@angular/router';
import {
  ERROR_MESSAGE,
  RegisterService,
  ValidateOTPInputDto
} from '@youtap-web-lib';
import {ModalService} from '../../../shared/service/modal/modal.service';
import {OtpModalComponent} from '../../login/otp-modal/otp-modal.component';
import {Idata} from './otp-register.component.dto';
import {EventData} from "../../../shared/data/event-data";
import {GaService} from "../../../shared/service/ga.service";

@Component({
  selector: 'otp-register',
  templateUrl: './otp-register.component.html'
})
export class OtpRegisterComponent implements OnInit, OnDestroy {
  second = 0;
  loadingOtp = false;
  errorInvalidOtp = false;
  error = false;
  errorTooMuchRequest = false;
  errorExpiredOtp = false;
  phone = '';
  data!: Idata;
  timerId: any;
  loginForm!: FormGroup;
  onReset = false

  constructor(
    private router: Router,
    private route: ActivatedRoute,
    private registerService: RegisterService,
    private modalService: ModalService,
    private formBuilder: FormBuilder,
    private eventData: EventData,
    private gaService: GaService,
  ) {
    this.loginForm = this.formBuilder.group({
      pin: [
        '',
        Validators.compose([Validators.required, Validators.maxLength(20)])
      ]
    });
  }

  ngOnInit(): void {
    this.countdown();
    this.phone = this.route.snapshot.queryParamMap.get('phone') ?? '';
  }

  onPinChanged(pin: string) {
    this.loadingOtp = true;
    const modalRef = this.modalService.open(OtpModalComponent, {
      title: '',
      closeButton: false
    });

    const data: ValidateOTPInputDto = {
      validateOTPInput: {
        otpCode: pin
      }
    };

    this.registerService.validateOTPMutation(data).subscribe({
      next: ({data}: any) => {
        this.loadingOtp = false;

        if (data) {
          modalRef.close('');
          this.gaService.addEvent('Register_otp_success', 'Register_otp', '')

          this.router.navigate(['/register/merchant'], {
            replaceUrl: true,
            queryParams: {
              phone: this.phone,
            }
          });
        }
      },
      error: (error: any) => {
        modalRef.close('');
        this.gaService.addEvent('Register_otp_' + error?.message, 'Register_otp', '')
        if (error?.message?.toLowerCase() === ERROR_MESSAGE.REGISTER_INVALID_OTP) {
          this.setErrorStates(true, false);
        }

        if (error?.message?.toLowerCase() === ERROR_MESSAGE.REGISTER_EXPIRED_OTP) {
          this.setErrorStates(false, true);
        }

        this.loadingOtp = false;
        this.error = true;
      }
    });
  }

  countdown() {
    if (!this.timerId) {
      // Check if timer is already running
      const expired = new Date(this.eventData.registerExpired)
      if (expired.getTime() < new Date().getTime()) {
        clearInterval(this.timerId); // Clear the timer when it reaches 0
        this.timerId = null; // Reset the timerId property
        this.setErrorStates(false, true);
      } else {
        this.second = Math.round(Math.abs(expired.getTime() - new Date().getTime()) / 1000);
        this.timerId = setInterval(() => {
          if (this.second > 0) {
            this.second--;
          } else {
            clearInterval(this.timerId); // Clear the timer when it reaches 0
            this.timerId = null; // Reset the timerId property
            this.gaService.addEvent('Register_otp_timeout', 'Register_otp', '')
          }
        }, 1000);
      }
    }
  }

  submitData() {
    this.loadingOtp = true;
    this.errorExpiredOtp = false
    this.errorInvalidOtp = false
    const modalRef = this.modalService.open(OtpModalComponent, {
      title: '',
      closeButton: false,
      data: {
        title: 'Sedang mengirim OTP',
        image: './assets/images/png/sending-otp.png'
      }
    });

    this.registerService.regenerateOTP().subscribe({
      next: ({data}: any) => {
        this.gaService.addEvent('Register_otp_failed_generate_success', 'Register_otp', '')
        this.loadingOtp = false;
        // this.second = 180;
        this.eventData.registerExpired = data.regenerateOTP.otpExpiredAt
        this.countdown();
        this.resetOtp()
        modalRef.close('');
      }, error: (error) => {
        this.gaService.addEvent('Register_otp_failed_generate_otp', 'Register_otp', '')
        this.loadingOtp = false;
        modalRef.close('');
      }
    })
  }


  resetOtp() {
    this.onReset = true
    setTimeout(() => {
      this.onReset = false
      this.error = false
    }, 0)
  }

  setErrorStates(invalidOtp: boolean, expiredOtp: boolean) {
    this.errorInvalidOtp = invalidOtp;
    this.errorExpiredOtp = expiredOtp;
  }

  ngOnDestroy() {
    this.eventData.registerExpired = ''
  }
}
