import {
  Component,
  EventEmitter,
  forwardRef,
  Input,
  Output
} from '@angular/core';
import { NG_VALUE_ACCESSOR } from '@angular/forms';
import { data } from 'autoprefixer';

@Component({
  selector: 'pin-input',
  templateUrl: './pin-input.component.html',
  styleUrls: ['./pin-input.component.scss'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => PinInputComponent),
      multi: true
    }
  ]
})
export class PinInputComponent {
  value = '';
  @Output() handleComplete: EventEmitter<string> = new EventEmitter<string>();
  @Output() handleChange: EventEmitter<string> = new EventEmitter<string>();
  @Output() errorMessage: EventEmitter<string> = new EventEmitter<string>();
  @Input() isError = false;
  @Input() disabled = false;
  @Input() length = 0;

  onCodeChanged(data: string) {
    this.value = data;
    this.isError = false;
    this.handleChange.emit(data);
    this.errorMessage.emit(data);
  }

  onChange: (data: string) => void = () => {
    data;
  };
  onTouch: (data: string) => void = () => {
    data;
  };

  changeDrop(data: string) {
    this.onChange(data);
    this.onTouch(data);
    this.handleComplete.emit(this.value);
  }

  // this method sets the value programmatically
  writeValue(value: string) {
    this.value = value;
  }

  // upon UI element value changes, this method gets triggered
  registerOnChange(fn: (data: string) => void) {
    this.onChange = fn;
  }

  // upon touching the element, this method gets triggered
  registerOnTouched(fn: (data: string) => void) {
    this.onTouch = fn;
  }
}
