/* eslint-disable @typescript-eslint/no-explicit-any */
import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute, NavigationExtras, Router } from '@angular/router';
import {
  BusinessCategory,
  CreateMerchantInputDto,
  ERROR_MESSAGE,
  RegisterService,
  ValidationService
} from '@youtap-web-lib';
import { TncModalComponent } from '../pin-register/tnc-modal/tnc-modal.component';
import { ModalService } from '../../../shared/service/modal/modal.service';
import { GaService } from '../../../shared/service/ga.service';
import {ReferralService} from "../../../shared/service/referral.service";
@Component({
  selector: 'merchant-register',
  templateUrl: './merchant-register.component.html'
})
export class MerchantRegisterComponent implements OnInit {
  registerForm!: FormGroup;
  loadingConfirm = false;
  error = false;
  phone = '';
  pinVisible = false;
  errorPIN = false;
  confirmPinVisible = false;
  check = false;
  errorMessage = '';
  errorReferral = '';
  errorPin = '';
  invalidReferral = false;

  constructor(
    private formBuilder: FormBuilder,
    private registerService: RegisterService,
    private router: Router,
    private route: ActivatedRoute,
    private modalService: ModalService,
    private gaService: GaService,
    private referralService: ReferralService
  ) {
    this.registerForm = this.formBuilder.group({
      name: [
        '',
        Validators.compose([Validators.required, Validators.maxLength(50)])
      ],
      businessName: [
        '',
        Validators.compose([Validators.required, Validators.maxLength(50)])
      ],
      businessCategory: ['', Validators.compose([Validators.required])],
      referralCode: [''],
      pin: [
        '',
        Validators.compose([
          Validators.required,
          Validators.maxLength(6),
          Validators.minLength(6),
          ValidationService.numValidator
        ])
      ],
      confirmPin: [
        '',
        Validators.compose([
          Validators.required,
          Validators.maxLength(6),
          Validators.minLength(6),
          ValidationService.numValidator
        ])
      ]
    });
  }
  ngOnInit(): void {
    this.phone = this.route.snapshot.queryParamMap.get('phone') ?? '';
    this.subscribeToReferralCodeChanges();
  }
    subscribeToReferralCodeChanges() {
      this.registerForm.get('referralCode')?.valueChanges.subscribe(() => {
        this.invalidReferral = false;
      });
    }

  submitData() {
    this.loadingConfirm = true;

    const stateData = {
      title: 'Selamat Kamu Sudah terdaftar!',
      isFromRegister: true
    };

    const navigationExtras: NavigationExtras = {
      state: stateData
    };

    const data: CreateMerchantInputDto = {
      createMerchantInputV2: {
        createMerchantInput: {
          businessCategory:
            this.registerForm.controls['businessCategory'].value.displayName,
          businessCategoryId:
            this.registerForm.controls['businessCategory'].value.id,
          businessName: this.registerForm.controls['businessName'].value,
          email: '',
          ownerName: this.registerForm.controls['name'].value,
          referralCode: this.registerForm.controls['referralCode'].value,
          skipOtp: true
        },
        createPinInput: {
          pin: this.registerForm.controls['pin'].value,
          confirmPin: this.registerForm.controls['confirmPin'].value,
           referralCodeAffiliate: this.referralService.getLastReferral() ? this.referralService.getLastReferral().referral : ''
        }
      }
    };

    this.registerService.createMerchant(data).subscribe({
      next: (data) => {
        this.loadingConfirm = false;
        if (data) {
          this.router.navigate(
            ['/register/success-register'],
            navigationExtras
          );
        }
        this.gaService.addEvent('Register_basic_success', 'Register_basic', '');
      },
      error: (error) => {
        this.loadingConfirm = false;
        const [graphqlError] = error.graphQLErrors;
        this.errorMessage = graphqlError?.message ? graphqlError?.message : '';
        this.gaService.addEvent(
          'Register_basic_' + this.errorMessage,
          'Register_basic',
          ''
        );
        if (
          this.errorMessage.toLowerCase() ===
            ERROR_MESSAGE.INVALID_REFERRAL.toLowerCase() ||
          this.errorMessage.toLowerCase() ===
            ERROR_MESSAGE.FAILED_ACTIVATION.toLowerCase()
        ) {
          this.errorReferral = 'Kode Referal tidak ditemukan';
          this.invalidReferral = true;
          this.errorPin = '';
        } else if (
          this.errorMessage.toLowerCase() ===
          ERROR_MESSAGE.ERROR_PIN.toLowerCase()
        ) {
          this.errorReferral = '';
          this.invalidReferral = false;
          this.errorPin = 'Pin tidak boleh berurutan atau digit yang sama';
        }
      }
    });
  }

  onChange: any = () => {
    this.errorPIN = false;
  };

  togglePinVisibility() {
    this.pinVisible = !this.pinVisible;
  }

  toggleConfirmPinVisibility() {
    this.confirmPinVisible = !this.confirmPinVisible;
  }

  tncModal() {
    const modalRef = this.modalService.open(TncModalComponent, {
      title: 'Syarat dan Ketentuan Umum Layanan',
      closeButton: true
    });
    modalRef.onResult().subscribe((closed) => {
      this.check = closed;
    });
  }

  handleCheck() {
    this.check = !this.check;
  }

  checkList(data: BusinessCategory) {
    this.registerForm.controls['businessCategory'].setValue(data);
  }
}
